.Services {}

.ServiceContainer {
    background-color: white;
    padding: 1rem;
    font-size: smaller;
    overflow: auto;
}

.ServiceSelect {
    padding: .5rem 0;
}

.ServiceInfo {
    padding-top: 1rem;
    margin-top: .5rem;
    border-top: 1px solid #eeeff0;
}

.ServiceTitle {
    font-size: larger;
    font-weight: bold;
}
.Title {
    font-weight: bold;
}

.Description {
    font-size: x-small;
    margin-bottom: .5rem;
}

.Param {
    padding: .5rem;
    border-bottom: 1px solid #eeeff0;
}

.Param:last-child {
    border-bottom: none;
}

.ParamInput {
    margin-top: .5rem;
}

.Execute {
    margin-top: 1rem;
    display: flex;
    justify-content: stretch;
}

.ExecuteButton {
    flex: 1;
}

.ExecuteButton:not(:last-child) {
    margin-right: .5rem;
}

.ProcessGraph {
    margin: .5rem;
    display: flex;
    flex-direction: column;
}

.ProcessGraphToggle, .ClipboardCopy {
    color: var(--secondary-disabled-color);
    display: flex;
    justify-content: flex-end;
}

.ProcessGraphToggle:hover, .ClipboardCopy:hover {
    cursor: pointer;
}

.ClipboardCopy {
    margin-top: .5rem;
    align-items: baseline;
}

