  --primary-color: rgb(0, 38, 56);
  --primary-contrast-color: #ffffff;
  --primary-hover: rgba(255, 255, 255, 0.13);

  --secondary-color: #FF2D28;
  --secondary-disabled-color: #FF5551;


}

.btn-primary {
  background-color: #FF2D28;
  background-color: var( --secondary-color);
  border-color: #FF2D28;
  border-color: var( --secondary-color);
}

.btn-primary:hover, .btn-primary:disabled {
  background-color: #FF5551;
  background-color: var(--secondary-disabled-color);
  border-color: #FF5551;
  border-color: var(--secondary-disabled-color);
}

.MainContainer {
